String.prototype.capitalize = function () {
  return this.toLowerCase().replace(/^\w/, (c) => c.toUpperCase());
};

Date.prototype.copy = function () {
  return new Date(this.valueOf());
};

Date.prototype.add = function (units, quantity) {
  if (quantity) {
    units = `${units}`.capitalize();
    const getter = `get${units}`,
      setter = `set${units}`;
    if (!getter || !setter) throw "cannot update requested quantity: " + units;
    const curValue = this[getter]();
    this[setter](curValue + quantity);
  }
  return this;
};
